


import React from 'react';
import styles from './loaderFullScreen.module.css';
import { BounceLoader } from 'react-spinners';


export default function LoaderFullScreen() {
    return <>
        <div className={styles.loader + " " + styles.hide } id='loader_full_screen'>
            <BounceLoader size={60} color='#183661'/>
        </div>
    </>

}
