

import stylesFromFullLoaderScteen from "./../Components/LoaderFullScreen/loaderFullScreen.module.css";

export function hideLoader() {
    const loader = document.getElementById("loader_full_screen");
    loader.classList.add(stylesFromFullLoaderScteen.hide);
}
export function showLoader() {
    const loader = document.getElementById("loader_full_screen");
    loader.classList.remove(stylesFromFullLoaderScteen.hide);
}
