


import React, { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';
import { useDispatch, useSelector } from 'react-redux';
import { whatIsActive } from '../../Redux/navbarSlice';
import styles from './login.module.css';
import { useFormik } from 'formik';
import axios from 'axios';
import { hideSmallLoading, showSmallLoading } from '../../Redux/smallLoadingSlice';
import { displayMessage } from '../../Redux/MessageSlice';
import { useNavigate } from 'react-router-dom';
import { setTokenValue, setUserName, login, setDataOfCart } from '../../Redux/userSlice';

export default function Login() {

  const navigate = useNavigate();
  const dispatcher = useDispatch();




  const formikLib = useFormik({
    initialValues: {
      email: "",
      password: ""
    },

    onSubmit: async (values) => {
      try {
        // Show Loading Screen
        showSmallLoading();

        //Send Request 
        const { data } = await axios.post("https://route-ecommerce.onrender.com/api/v1/auth/signin", values)
        const response = data;

        // get cart's data
        try {
          const { data } = await axios.get("https://route-ecommerce.onrender.com/api/v1/cart", {
            headers: {
              token: response.token,
            }
          });
          const price = data.data.totalCartPrice
          const products = data.data.products;
          const countCart = data.numOfCartItems
          dispatcher(setDataOfCart({ price, products, countCart }))
        }
        catch (err) {

        }

        //set login
        dispatcher(setTokenValue(response.token));
        dispatcher(setUserName());
        dispatcher(login());

        // set Token in Local Storeage
        if (document.getElementById("rememberMe").checked)
          localStorage.setItem("token", response.token);

        // Hide Loading Screen 
        hideSmallLoading();

        // Show Message To User
        displayMessage("success", `Welcome ${response.user.name}`);

        //Go to Home page
        navigate("/", { replace: true });
      }
      catch (error) {
        if (error.message === "Network Error") {
          displayMessage("error", "please check your internet")
        }
        else if (error.message === "Request failed with status code 401") {
          displayMessage("error", error.response.data.message)
        }
        else {
          displayMessage("error", "Unknown Error")
        }
        hideSmallLoading();
      }
    }

  })




  useEffect(() => {
    dispatcher(whatIsActive("login"));
  }, [])



  //State 
  const [passwordStatus, setPasswordStatus] = useState(false);

  
  return <>
    <Helmet>
      <title>Login</title>
    </Helmet>
    <div className={styles.login + " px-sm-0 px-3 container-fluid d-flex justify-content-center align-items-center "}>
      <span className={styles.overlay}></span>

      <form onSubmit={formikLib.handleSubmit}>
        <div className={styles.parent_content}>
          <span className={styles.title + ' h4 text-center d-block m-0 mb-4'}> Login </span>
          <div className={styles.inputs + " mb-3"}>
            <input type="email" placeholder='Email' value={formikLib.values.email} onChange={formikLib.handleChange} name='email' />
            <div>
              <i className="fa-solid fa-at"></i>
            </div>
          </div>
          <div className={styles.inputs + " mb-3 "}>
            <input type={passwordStatus?"text":"password"} placeholder='password' value={formikLib.values.password} onChange={formikLib.handleChange} name='password' />
            <div>
              {formikLib.values.password.length >= 1 ?
                passwordStatus?<i className="fa-solid fa-eye " role="button" onClick={()=>{setPasswordStatus(false)}}></i>:<i className=" fa-solid fa-eye-slash" role="button" onClick={()=>{setPasswordStatus(true)}}></i>
                :
                <i className="fa-solid fa-lock"></i>}
            </div>
          </div>
          <div className={styles.rememberMe}>
            <input type="checkbox" className='mx-2' id='rememberMe' />
            <label htmlFor="rememberMe">Remember me</label>
          </div>
          <div className={'text-center ' + styles.btn}>
            <button className='py-1 px-3 rounded-pill border-0' type='submit'> Login </button>
          </div>
        </div>
      </form>
    </div>
  </>
}
