


import React, { useEffect } from 'react';
import { Helmet } from 'react-helmet';
import { useDispatch, useSelector } from 'react-redux';
import { whatIsActive } from '../../Redux/navbarSlice';
import styles from './brands.module.css'
import { BounceLoader } from 'react-spinners';
import { getBrands } from '../../Redux/brandSlice';


export default function Brands() {

  const dispatcher = useDispatch();
  const { products } = useSelector(reducers => reducers.brandReducer);

  useEffect(() => {
    dispatcher(whatIsActive("brands"));
    dispatcher(getBrands());

  }, [])

  return <>
    <Helmet>
      <title>Brands</title>
    </Helmet>

    {
      products ? <div className={styles.brands}>
        <div className="container">
          <div className="row gy-3">
            {
              products.map((e, i) => {
                return <div key={i} className="col-md-4 col-lg-3">
                  <div>
                    <img src={e.image} className='w-100' alt="" />
                  </div>
                </div>
              })
            }
          </div>
        </div>
      </div>
        :
        <div className='d-flex vh-100 justify-content-center align-items-center'>

          <BounceLoader color="#183661" />

        </div>
    }

  </>

}
