



import React from 'react';
import styles from './product.module.css';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { addProduct } from '../../Redux/userSlice';

export default function Product({ img1, img2, price, brand, title, priceAfterDiscount, id }) {

    const  navigate = useNavigate();
    const dispatcher = useDispatch();
    const { token } = useSelector( reducers => reducers.userReducer );

    return <>
        <div
            className={styles.product}
            onMouseEnter={() => {
                const image = document.getElementById(id);
                image.setAttribute("src", img2)
            }}
            onMouseLeave={() => {
                const image = document.getElementById(id);
                image.setAttribute("src", img1)
            }}>

            <div className='position-relative'>
                <img src={img1} alt={title} className='w-100' id={id} />
                <div className={styles.btns + " d-flex justify-content-center"}>
                    <button className={styles.addBtn + " mx-2"} title='add to cart'
                    onClick={()=>{
                        dispatcher(addProduct({token , id}));
                    }}
                    > <i className="fa-solid fa-plus"></i> </button>
                    
                    <button className={styles.addBtn + " mx-2"} title='show details' onClick={()=>{
                        navigate(`/product/${id}`);
                    }}> <i className="fa-solid fa-circle-info"></i> </button>
                
                </div>
            </div>
            <span className={styles.brandName + " d-block mt-4 "}> {brand} </span>
            <span className='d-block '>
                {
                    (title.split(" ").slice(0, 3).join(" ").length > 26) ?
                        <>{title.slice(0, 26)}...</>
                        : <>{title.split(" ").slice(0, 3).join(" ")}</>
                }
            </span>
            <span className='mt-1 d-block'>
                {priceAfterDiscount ? <><span className={styles.discount}>${price}</span> <br /> <span>${priceAfterDiscount} EGP</span></> : <><span>${price} EGP</span></>}
            </span>

            {
                priceAfterDiscount ? <span className={styles.offer}>
                    - {Math.trunc(((price - priceAfterDiscount) / price * 100))}%
                </span> : null
            }
        </div>
    </>
}
