import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import jwtDecode from "jwt-decode";
import { hideLoader, showLoader } from "./loaderSlice";
import { displayMessage } from "./MessageSlice";
import { hideSmallLoading, showSmallLoading } from "./smallLoadingSlice";



export const checkToken = createAsyncThunk("user/checkToken", async () => {
    showLoader();
    try {
        const { data } = await axios.get("https://route-ecommerce.onrender.com/api/v1/cart", {
            headers: {
                token: localStorage.getItem("token"),
            }
        })
        hideLoader();
        return data
    } catch (err) {
        if (err.response) {
            if (err.response.data.message === "Invalid Token. please login again") {
                hideLoader();
                return "noLogin";
            }
            else {
                hideLoader();
                return { data: "login" };
            }
        }
        else {
            hideLoader();
        }
    }

})

export const clearCart = createAsyncThunk("user/clearCart", async (userToken) => {
    showSmallLoading();
    try {
        const { data } = await axios.delete("https://route-ecommerce.onrender.com/api/v1/cart", {
            headers: {
                token: userToken,
            }
        })
        hideSmallLoading();
        return data.message
    } catch (err) {
        hideSmallLoading();
    }
})


export const addProduct = createAsyncThunk("user/addProduct", async (details, { getState, dispatch }) => {
    const { userReducer } = getState();

    if (userReducer.canIAdd) {
        dispatch(user.actions.toggleStatusAdd());
        try {
            const { data } = await axios.post("https://route-ecommerce.onrender.com/api/v1/cart", {
                "productId": details.id
            }, {
                headers: {
                    token: details.token
                }
            })
            displayMessage("success", data.message)
            dispatch(user.actions.toggleStatusAdd());
            return data
        }
        catch (err) {
            if (err.message == "Request failed with status code 401") {
                dispatch(user.actions.toggleStatusAdd());
                displayMessage("error", " Go to login and come back");
            }
            else {
                dispatch(user.actions.toggleStatusAdd());
                displayMessage("error", " Unknown error ");
            }
            return null
        }

    }
    else {
        displayMessage("warning", "wait");
    }

})




const user = createSlice({
    name: "user",

    initialState: {
        isLogin: false,
        token: (localStorage.getItem("token") ? localStorage.getItem("token") : null),
        userName: null,
        countCart: 0,
        price: 0,
        products: null,
        canIAdd: true,
    },

    reducers: {
        toggleStatusAdd: (state) => {
            if (state.canIAdd === true)
                state.canIAdd = false
            else
                state.canIAdd = true
        },
        setTokenValue: (state, action) => {
            state.token = action.payload;
        },
        setUserName: (state) => {
            state.userName = jwtDecode(state.token).name;
        },
        login: (state) => {
            state.isLogin = true;
        },
        logout: (state) => {
            state.isLogin = false;
            state.token = null;
            state.products = null;
            state.userName = null;
            state.countCart = 0;
            state.price = 0;
            localStorage.removeItem("token");
        },
        setDataOfCart: (state, { payload }) => {
            state.price = payload.price;
            state.products = payload.products;
            state.countCart = payload.countCart;
        },
        setProducts: (state,{payload})=>{
            state.products = payload;
        },
        setCountCart:(state,{payload})=> {
            state.countCart = payload;
        },
        setPrice:(state,{payload})=> {
            state.price = payload;
        },

    },

    extraReducers: builder => {
        builder.addCase(checkToken.fulfilled, (state, { payload }) => {
            if (payload == "noLogin") {
                state.token = null;
                state.isLogin = false;
                state.userName = null;
            }
            else if (payload) {
                state.token = localStorage.getItem("token");
                state.isLogin = true;
                state.userName = jwtDecode(localStorage.getItem("token")).name;

                if (payload.numOfCartItems !== undefined)
                    state.countCart = payload.numOfCartItems;
                else
                    state.countCart = 0;

                if (payload.data.totalCartPrice)
                    state.price = payload.data.totalCartPrice;
                else
                    state.price = 0;

                if (payload.data.products)
                    state.products = payload.data.products;
                else
                    state.products = null;
            }
        });


        builder.addCase(addProduct.fulfilled, (state, { payload }) => {
            if (payload) {
                state.countCart = payload.numOfCartItems;
                state.price = payload.data.totalCartPrice;
            }
        });

        builder.addCase(clearCart.fulfilled, (state, { payload }) => {
            if (payload) {
                state.price = 0;
                state.products = null;
                state.countCart = 0;
                displayMessage("success", payload);
            }
            else
                displayMessage("maybe you have a problem");
        });
    }
})

export const userReducer = user.reducer;
export const { setTokenValue, setUserName, login, setPrice, setCountCart, logout, setDataOfCart, setProducts } = user.actions;