


import React, { useEffect } from 'react';
import { Helmet } from 'react-helmet';
import styles from './contact.module.css';
import { useDispatch, useSelector } from 'react-redux';
import { whatIsActive } from '../../Redux/navbarSlice';


export default function Contact() {

  const dispatcher = useDispatch();
  //Redux Data
  //const {whatIsActive} = useSelector( (reducers) => reducers.navbarReducer );



  //setEvents 
  function clickToCopy() {
    const elemnts = Array.from(document.querySelectorAll(`.${styles.myData} > div > div`));

    elemnts.forEach(e => {
      e.addEventListener("click", () => {
        navigator.clipboard.writeText(e.children[0].children[1].innerHTML)
      })
    })
  }



  //Component did mount
  useEffect(function () {

    //events
    clickToCopy();
    dispatcher(whatIsActive("contact"))


  }, [])


  return <>
    <Helmet>
      <title>Contact me</title>
    </Helmet>
    <div className={'d-flex justify-content-center align-items-center p-5 text-center ' + styles.title}>
      Contact With Abdelrhman Khalifa
    </div>
    <div className={styles.contact + " "}>
      <div className={'container-fluid d-flex justify-content-center align-items-center ' + styles.overlay}>
        <div className={styles.myData + " "}>
          <div className='d-flex flex-column align-items-center'>
            <img src={require("./../../assets/developer.jpeg")} alt="" />
            <div className='d-flex align-items-center border-bottom py-2 w-100 justify-content-center'>
              <div title='click to copy'>
                <i className="fa-brands fa-whatsapp"></i>
                <span className='mx-3'>01025481400</span>
              </div>
            </div>
            <div className='d-flex align-items-center py-2 w-100 justify-content-center'>
              <div title='click to copy'>
                <i className="fa-solid fa-at"></i>
                <span className='mx-3'>mezoozem2002@gmail.com</span>
              </div>
            </div>
            <div className='d-flex align-items-center border-bottom py-2 w-100 justify-content-center'>
              <div title='click to copy'>
                <i className="fa-solid fa-at"></i>
                <span className='mx-3'>poda5371@gmail.com</span>
              </div>
            </div>
            <div className='d-flex align-items-center py-2 w-100 justify-content-center'>
              <div>
                <i className="fa-brands fa-facebook"></i>
                <a className='mx-3' href='https://www.facebook.com/profile.php?id=100091459810709'>facebook</a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </>
}
