


import React, { useEffect } from 'react';
import { Helmet } from 'react-helmet';
import { useDispatch } from 'react-redux';
import { whatIsActive } from '../../Redux/navbarSlice';
import styles from './register.module.css';
import { useFormik } from 'formik';
import axios from 'axios';
import { hideSmallLoading, showSmallLoading } from '../../Redux/smallLoadingSlice';
import { displayMessage } from '../../Redux/MessageSlice';
import { useNavigate } from 'react-router-dom';
import { useState } from 'react';





export default function Register() {
  
  const dispatcher = useDispatch();
  const navigate = useNavigate();

  const formikLib = useFormik({

    initialValues: {
      name: "",
      email: "",
      password: "",
      rePassword: "",
      phone: ""
    },

    onSubmit: async (values) => {
      const x = axios
      try {
        showSmallLoading();
        const { data } = await axios.post("https://route-ecommerce.onrender.com/api/v1/auth/signup",
          values,
          { timeout: 10000 });
        console.log(data);
        hideSmallLoading();
        displayMessage("success", data.message);
        navigate("/login",{replace:true});
      }
      catch (error) {
        console.log(error);
        if (error.message == "Network Error") {
          displayMessage("error", "please check your internet");
        }
        else if (error.message == "Request failed with status code 409") {
          displayMessage("error", error.response.data.message);
        }
        else {
          displayMessage("error", "Unknown Error");
        }

        hideSmallLoading();
      }

    },

    validate: (values) => {
      console.log(values);
      let errors = {};
      const regexName = /^[A-Za-z]{3,13}(\s{1}[A-Za-z]{3,13})?$/;
      const regexEmail = /^\w+([\.-]?\w+)*@gmail.com$/;
      const regexPassword = /^\w{8,}$/;
      const regexPhone = /^01[0125][0-9]{8}$/;

      if (!regexName.test(values.name)) {
        errors.name = "Name not allowed";
      }
      else if (values.name.length < 3 || values.name.length >= 26) {
        errors.name = "Number of characters in name field not allowed"
      }
      else if (values.name == "") {
        errors.name = "Name required"
      }
      if (!regexEmail.test(values.email)) {
        errors.email = " Email invalid"
      }
      if (!regexPassword.test(values.password)) {
        errors.password = "Password Must be min 8 characters"
      }
      if (!(values.password == values.rePassword && regexPassword.test(values.rePassword))) {
        errors.rePassword = "Password not matched"
      }
      if (!regexPhone.test(values.phone)) {
        errors.phone = "number phone invalid"
      }

      return errors
    }
  });

  //State 
  const [passwordStatus,setPasswordStatus] = useState(false);

  useEffect(() => {
    dispatcher(whatIsActive("register"))
  }, []);

  return <>
    <Helmet>
      <title>Register</title>
    </Helmet>
    <div className={styles.register + " px-sm-0 px-3 container-fluid d-flex justify-content-center align-items-center "}>
      <span className={styles.overlay + " d-block "}></span>

      <form onSubmit={formikLib.handleSubmit}>
        <div className={styles.parent_content}>
          <span className={styles.title + ' h4 text-center d-block m-0 mb-4'}> Register </span>
          <div className={`${styles.inputs} + " mb-3 " + ${(formikLib.errors.name|| formikLib.values.name.length === 0) ? '' : styles.correct}`}>
            <input type="text" placeholder='Personal name' onBlur={formikLib.handleBlur} value={formikLib.values.name} onChange={formikLib.handleChange} name='name' />
            <div>
              <i className="fa-regular fa-user"></i>
            </div>
          </div>
          <div className={`${styles.inputs} + " mb-3 " + ${(formikLib.errors.email|| formikLib.values.email.length === 0) ? '' : styles.correct}`}>
            <input type="email" placeholder='Email' onBlur={formikLib.handleBlur} value={formikLib.values.email} onChange={formikLib.handleChange} name='email' />
            <div>
              <i className="fa-solid fa-at"></i>
            </div>
          </div>


          <div className={`${styles.inputs} + " mb-3 " + ${(formikLib.errors.password || formikLib.values.password.length === 0) ? '' : styles.correct}`}>
            <input type={passwordStatus?"text":"password"}  placeholder='password' onBlur={formikLib.handleBlur} value={formikLib.values.password} onChange={formikLib.handleChange} name='password' />
            <div>
              {formikLib.values.password.length >= 1 || formikLib.values.rePassword.length >= 1?
                passwordStatus?<i className="fa-solid fa-eye " role="button" onClick={()=>{setPasswordStatus(false)}}></i>:<i role="button" className="fa-solid fa-eye-slash" onClick={()=>{setPasswordStatus(true)}}></i>
                :
                <i className="fa-solid fa-lock" ></i>
              }
            </div>
          </div>


          <div className={`${styles.inputs} + " mb-3 " + ${(formikLib.errors.rePassword|| formikLib.values.rePassword.length === 0) ? '' : styles.correct}`}>
            <input type={passwordStatus?"text":"password"} placeholder='re-password'  onBlur={formikLib.handleBlur} value={formikLib.values.rePassword} onChange={formikLib.handleChange} name="rePassword" />
            <div>
              {formikLib.values.password.length >= 1 || formikLib.values.rePassword.length >= 1 ?
                passwordStatus?<i className="fa-solid fa-eye " role="button" onClick={()=>{setPasswordStatus(false)}}></i>:<i className=" fa-solid fa-eye-slash" role="button" onClick={()=>{setPasswordStatus(true)}}></i>
                :
                <i className="fa-solid fa-lock"></i>}
            </div>
          </div>


          <div className={`${styles.inputs} + " mb-3 " + ${(formikLib.errors.phone || formikLib.values.phone.length === 0) ? '' : styles.correct}`}>
            <input type="tel" placeholder='phone number' onBlur={formikLib.handleBlur} onChange={formikLib.handleChange} value={formikLib.values.phone} name='phone' />
            <div>
              <i className="fa-solid fa-phone"></i>
            </div>
          </div>


          <div className={styles.error}>
            {
              (formikLib.errors.email || formikLib.errors.name || formikLib.errors.password || formikLib.errors.rePassword || formikLib.errors.phone) ?
                (formikLib.errors.name && formikLib.touched.name) ?
                  <p className='text-center'>
                    <i className="fa-solid fa-xmark mx-1"></i>
                    {formikLib.errors.name}
                  </p>
                  :
                  (formikLib.errors.email && formikLib.touched.email) ?
                    <p className='text-center'>
                      <i className="fa-solid fa-xmark mx-1"></i>
                      {formikLib.errors.email}
                    </p> :
                    (formikLib.errors.password && formikLib.touched.password) ?
                      <p className='text-center'>
                        <i className="fa-solid fa-xmark mx-1"></i>
                        {formikLib.errors.password}
                      </p> :
                      (formikLib.errors.rePassword && formikLib.touched.rePassword) ?
                        <p className='text-center'>
                          <i className="fa-solid fa-xmark mx-1"></i>
                          {formikLib.errors.rePassword}
                        </p> :
                        (formikLib.errors.phone && formikLib.touched.phone) ?
                          <p className='text-center'>
                            <i className="fa-solid fa-xmark mx-1"></i>
                            {formikLib.errors.phone}
                          </p> : ""
                : null
            }
          </div>
          <div className={'text-center ' + styles.btn}>
            <button className='py-1 px-3 rounded-pill border-0' type='submit'> Register </button>
          </div>
        </div>
      </form>
    </div>
  </>

}