


import React, { useEffect } from 'react';
import styles from './scrollbar.module.css';
import { displayMessage } from '../../Redux/MessageSlice';


export default function Scrollbar() {

    function ControlScrollbarHeight() {
        const scrollbar = document.getElementById("scrollbar_inside");
        const screenHeight = window.innerHeight;
        const pageHeight = document.body.scrollHeight;
        const value = ((screenHeight + window.pageYOffset) / pageHeight) * 100;
        if (window.pageYOffset === 0)
           scrollbar.style.height = `0%`; 
        else
            scrollbar.style.height = `${value}%`;
    }

    useEffect(() => {
        window.addEventListener("scroll", ControlScrollbarHeight)
    }, [])

    return <div className={styles.scrollbar}>
        <div id='scrollbar_inside' className={styles.scrollbar_inside}>

        </div>
        <div className={styles.customScroll}>
            <button className='rounded-pill' onClick={()=>{
                displayMessage("warning", " This feature is not yet available ")
            }}> Scroll  Speeding</button>
            <div>

            </div>
        </div>
    </div>
}
