


import React from 'react';
import styes from './message.module.css';



export default function MessageToUser() {
    return <div className={styes.message}>

        <div className={'d-flex ' + styes.warning} id='warning'>
            <i className="fa-solid fa-triangle-exclamation"></i>
            <span className='flex-grow-1 text-center'>Warning safdas fdsaf dsaf dsaf asdfdasf ad fadsf adsf </span>
        </div>

        <div id='success' className={styes.success + " d-flex "}>
            <i className="fa-solid fa-circle-check"></i>
            <span className='flex-grow-1 text-center'>Lorem ipsum, dolor sit amet consectetur
                adipisicing elit. Dignissimos laborum </span>
        </div>

        <div className={styes.error + ' d-flex'} id='error'>
         <i className="fa-solid fa-xmark"></i>
            <span className='flex-grow-1 text-center'>Lorem ipsum, dolor sit amet consectetur
                adipisicing elit. Dignissimos laborum
            </span>
        </div>
    </div>

}
