



import React from 'react';
import styles from './navbar.module.css';
import { Link, useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';

export default function Navbar() {

  const navigate = useNavigate();
  const { isLogin ,countCart } = useSelector(reducers => reducers.userReducer);

  return <>
    <nav className={"navbar navbar-expand-lg py-lg-0 py-3 " + styles.navbar}>
      <div className="container-fluid px-lg-3 px-0">
        <div className='d-flex justify-content-between flex-lg-grow-0 flex-grow-1 px-lg-0 px-4 mb-lg-0 align-items-center'>
          <a className="navbar-brand" to="#"><div><img src={require("./../../assets/logo.png")} alt="" /></div></a>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarRouter" aria-controls="navbarRouter" aria-expanded="false" aria-label="Toggle navigation">
            <div
              className={styles.navbar_icons + ' d-flex flex-column'}
              onClick={() => {
                document.querySelector(`.${styles.navbar_icons}`).classList.toggle(styles.active)
              }}>
              <span></span>
              <span></span>
              <span></span>
            </div>
          </button>
        </div>
        <div className="collapse navbar-collapse " id="navbarRouter">
          <ul className={"navbar-nav me-auto mb-2 mb-lg-0  " + styles.first_ul}>
            <li className="nav-item" id='shopping'>
              <Link className="nav-link" aria-current="page" to="/">SHOPPING</Link>
            </li>
            <li className="nav-item" id='brands'>
              <Link className="nav-link" to="/brands">Brands</Link>
            </li>
            <li className="nav-item " id='contact'>
              <Link className="nav-link" to="/contact">CONTACT ME</Link>
            </li>
            {
              isLogin ?
                null
                :
                <>
                  <li className="nav-item" id='login'>
                    <Link className="nav-link" to="/login">LOGIN</Link>
                  </li>
                  <li className="nav-item" id='register'>
                    <Link className="nav-link" to="/register">REGISTER</Link>
                  </li>
                </>
            }

          </ul>

          {
            isLogin ?
              <ul className={'navbar-nav ms-auto mb-2 mb-lg-0 px-2 ' + styles.second_ul}>
                <li className='nav-item justify-content-start mb-lg-0 mb-3'>
                  <i className="fa-regular fa-user " onClick={() => {
                    navigate("/profile")
                  }}></i>
                </li>
                <li className={' nav-item mx-0 justify-content-start mx-lg-3 ' + styles.cartIcon}>
                  <i className="fa-solid fa-cart-shopping" onClick={() => {
                    navigate("/cart")
                  }}><span> {countCart} </span></i>
                </li>
              </ul>
              :
              null
          }
        </div>
      </div>
    </nav>
  </>
}
