import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";




export const getAllProduct = createAsyncThunk(
    "products/getAllProducts" ,
    async (page) => {
        const { data } = await axios.get("https://route-ecommerce.onrender.com/api/v1/products",{
            params: {
                "page" : page
            },
        });
        return data
    }
);


const productsSlice = createSlice({
    name:"products",
   
    initialState: {
        allProduct:null,
        currentPage:1,
        numberOfPages:1,
    },

    reducers:{
        setNull:(state)=>{
            state.allProduct = null;
        },
        setCurrentPage: (state,{payload}) => {
            state.currentPage = payload ;
        }
    },

    extraReducers: (builder) => {

        builder.addCase(getAllProduct.fulfilled , (state,{payload})=>{
            state.allProduct = payload;
            state.numberOfPages = payload.metadata.numberOfPages;
            state.currentPage = payload.metadata.currentPage;
        })

    }
});

export const productsReducer = productsSlice.reducer ;
export const {setNull , setCurrentPage} = productsSlice.actions ;