import { createSlice } from "@reduxjs/toolkit";



//css to another components
import styles_From_Navbar from "./../Components/Navbar/navbar.module.css";

const navbarSlice = createSlice({

    name:"navbar",

    initialState:{
        counter:0,
    },

    reducers:{
        whatIsActive:(state,{payload})=>{
            const elements = Array.from(document.querySelectorAll(`.${styles_From_Navbar.navbar} li`));
            elements.forEach( e => {
                e.classList.remove(`${styles_From_Navbar.active}`);
            })
            if(payload)
                document.getElementById(payload).classList.add(`${styles_From_Navbar.active}`);
        },
    }
}) 

export const navbarReducer = navbarSlice.reducer;
export const {plus , whatIsActive} = navbarSlice.actions;