import logo from './logo.svg';
import './App.css';
import { Navigate, RouterProvider, createHashRouter } from 'react-router-dom';
import Layout from './Components/Layout/Layout';
import Shopping from './Components/Shopping/Shopping';
import Contact from './Components/Contact/Contact';
import Login from './Components/Login/Login';
import Register from './Components/Register/Register';
import Brands from './Components/Brands/Brands';
import { Provider, useSelector } from 'react-redux';
import { store } from './Redux/mainStore';
import ProductDetails from './Components/ProductDetails/ProductDetails';
import Cart from './Components/Cart/Cart';
import Profile from './Components/Profile/Profile';



function App() {

  const routes = createHashRouter([
    {
      path: "/", element: <Layout />, children: [
        { path: "/", element: <Shopping /> },
        { path: "/contact", element: <Contact />},
        { path: "/cart", element:  <ProtectedRoute> <Cart /> </ProtectedRoute>  },
        { path: "/profile", element: <ProtectedRoute > <Profile /> </ProtectedRoute> },
        { path: "/login", element: <Login /> },
        { path: "/register", element: <Register /> },
        { path: "/brands", element: <Brands /> },
        { path: "/product/:id/", element:  <ProductDetails /> },
      ]
    },
    {
      path:"*",element: <div className='vh-100 d-flex justify-content-center align-items-center'> 
        <img src={require("./assets/Not-found.jpg")} style={window.innerWidth > 1162? {maxWidth:"25%"}:window.innerWidth > 560?{maxWidth:"50%"}:{maxWidth:"100%"}} alt="Not Found Page" />
      </div>
    }
  ])


  function ProtectedRoute({ children }) {
    const { isLogin } = useSelector( reducers => reducers.userReducer ) ;



    return <>
      {
        isLogin ? <> { children } </> 
        :
        <Navigate to={"/login"}/>
      }
    </>
  }

  return <>
    <Provider store={store}>
      <RouterProvider router={routes} />
    </Provider>
  </>
}

export default App;
