import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";
import { displayMessage } from "./MessageSlice";




export const getBrands = createAsyncThunk("brand/getBrands", async () => {

    try {
        const { data } = await axios.get("https://route-ecommerce.onrender.com/api/v1/brands");
        return data.data;
    }
    catch (err) {
        displayMessage("warning" , " there an unknown problem with data try again");
    }

}
)


const brandSlice = createSlice(
    {
        name: "brand",

        initialState: {
            products: null
        },

        extraReducers: builder => {

            builder.addCase(getBrands.fulfilled, (state, action) => {
                const response = action.payload;
                state.products = response;
            })


        }
    }
)

export const brandReducer = brandSlice.reducer;