



import React from 'react';
import Navbar from '../Navbar/Navbar';
import { Outlet } from 'react-router-dom';
import Scrollbar from '../Scrollbar/Scrollbar';
import Footer from '../Footer/Footer';
import SmallLoading from '../SmallLooading/SmallLoading';
import MessageToUser from '../MessageToUser/MessageToUser';
import LoaderFullScreen from '../LoaderFullScreen/LoaderFullScreen';
import { checkToken } from '../../Redux/userSlice';
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';

export default function Layout() {

  const dispatcher = useDispatch();

  // sprecial to check status user login or no
  const { isLogin } = useSelector((reducers) => reducers.userReducer);
  useEffect(() => {
    if (localStorage.getItem("token") !== null && !isLogin) {
      dispatcher(checkToken());
    }
  }, []);


  return <>
    <Navbar />
    <MessageToUser />
    <Scrollbar />
    <LoaderFullScreen />
    <SmallLoading />
    <Outlet />
    <Footer />
  </>
}
