



import React from 'react';
import styles from './profile.module.css';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import { logout } from '../../Redux/userSlice';
import { useLayoutEffect } from 'react';
import { whatIsActive } from '../../Redux/navbarSlice';

export default function Profile() {


  const dispatcher = useDispatch();
  const navigate = useNavigate();
  const { userName } = useSelector(reducers => reducers.userReducer);

  useLayoutEffect(() => {
    dispatcher(whatIsActive());
  }, [])

  return <>
    <Helmet>
      <title>Profile</title>
    </Helmet>
    <div className={'container-fluid ' + styles.profile}>
      <div className="row gy-3">
        <div className="col-md-4 d-flex justify-content-center">
          <img src={require("./../../assets/podaa.jpg")} className={styles.user_img} alt="" />
        </div>
        <div className="col-md-8 px-sm-0 px-5">
          <span className={styles.name + ' d-block h4'}>Name : {userName}</span>
          <span className='d-block h6 mb-5'>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Accusamus, cum excepturi quisquam necessitatibus fuga reprehenderit quas. Perspiciatis dolores doloribus sit.</span>
          <button onClick={() => {
            dispatcher(logout());
            navigate("/login", { replace: true });
          }}> <i className="fa-solid fa-arrow-right-from-bracket"></i> Logout</button>
        </div>
      </div>
    </div>

  </>
}
