




import React from 'react';
import styles from "./productDetails.module.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from 'react-slick';
import axios from 'axios';
import { useEffect } from 'react';
import { useState } from 'react';
import { BounceLoader } from 'react-spinners';
import { useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { displayMessage } from '../../Redux/MessageSlice';
import { Helmet } from 'react-helmet';
import { addProduct } from '../../Redux/userSlice';

export default function ProductDetails() {

    const { token } = useSelector(reducers => reducers.userReducer);

    const { id } = useParams();


    const dispatcher = useDispatch();
    const [product, setProduct] = useState(null);

    async function getProductDetails(id) {
        try {
            const { data } = await axios.get(`https://route-ecommerce.onrender.com/api/v1/products/${id}`);
            setProduct(data.data);
            console.log(data.data);
        } catch (err) {
            console.log(err);
        }
    }

    useEffect(() => {
        getProductDetails(id);
    }, [])


    const settings = {
        dots: true,
        infinite: true,
        speed: 200,
        slidesToScroll: 1,
        centerPadding: "60px",
        swipeToSlide: true,
        responsive: [
            {
                breakpoint: 9000,
                settings: {
                    slidesToShow: 3,
                }
            },
            {
                breakpoint: 991,
                settings: {
                    slidesToShow: 2,

                }
            },
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 1,
                }
            }
        ]
    }

    return <div>
        {product ? <Helmet><title>{product.title}</title></Helmet> : <Helmet><title>product</title></Helmet>}

        {
            product ? <div className={styles.ProductDetails + " container-fluid"}>
                <div className="container">
                    <div className="row gy-4">
                        <div className="col-md-4">
                            <img src={product.imageCover} className='w-100' alt="" />
                        </div>
                        <div className="col-md-8">
                            <span className={styles.title + " d-block h4"}>{product.title}</span>
                            <span className={styles.desc + " d-block h6"}>
                                {product.description}
                            </span>
                            {
                                product.priceAfterDiscount ?
                                    <span className={styles.price + ' d-block h6 mb-4'}> price : ${product.priceAfterDiscount} EGP</span>
                                    :
                                    <span className={styles.price + ' d-block h6 mb-4'}> price : ${product.price} EGP</span>
                            }
                            <div className={styles.details + " d-flex flex-wrap mb-3"}>
                                {product.sold ? <span className='rounded-pill mx-1 mx-sm-2 mb-1'>sold {product.sold}</span> : null}
                                {product.brand ? <span className='rounded-pill mx-1 mx-sm-2 mb-1'>{product.brand.name}</span> : null}
                                {product.quantity ? <span className='rounded-pill mx-1 mx-sm-2 mb-1'>count {product.quantity}</span> : null}
                                {product.category ? <span className='rounded-pill mx-1 mx-sm-2 mb-1'>{product.category.name}</span> : null}
                            </div>
                            <div className={styles.buttons + " d-flex flex-wrap mb-4"}>
                                <button className={styles.add_to_cart + " rounded-pill me-2"}
                                    onClick={() => {
                                        console.log("Added product");
                                        dispatcher(addProduct({token,id}));
                                    }}
                                >Add to cart</button>

                                <button className={styles.like + " rounded-pill"} onClick={() => {
                                    const element = document.getElementById("like-product");
                                    if (element.classList.contains("fa-regular")) {
                                        element.classList.remove("fa-regular");
                                        element.classList.add("fa-solid");
                                    }
                                    else {
                                        element.classList.add("fa-regular");
                                        element.classList.remove("fa-solid");
                                    }
                                }}>Like <i className="fa-regular fa-heart" id="like-product"></i> </button>

                            </div>
                            <div>
                                <Slider {...settings}>
                                    {
                                        product.images.map((e, i) => {
                                            return <div key={i} className={styles.photos + " px-2"}>
                                                <img src={e} className={styles.product_photo + " w-100"} alt="" />
                                            </div>
                                        })
                                    }
                                </Slider>
                            </div>
                        </div>
                    </div>
                </div>
            </div >
                :
                <div className={styles.waiting + " d-flex justify-content-center align-items-center"}>
                    <BounceLoader size={60} color='#183661' />
                </div>
        }
    </div>
}


