


import React, { useEffect, useState } from 'react';
import styles from './cart.module.css';
import { useDispatch, useSelector } from 'react-redux';
import { Helmet } from 'react-helmet';
import { clearCart, setCountCart, setPrice, setProducts } from '../../Redux/userSlice';
import axios from 'axios';
import { displayMessage } from '../../Redux/MessageSlice';
import { whatIsActive } from '../../Redux/navbarSlice';
import { useLayoutEffect } from 'react';


export default function Cart() {

    const dispatcher = useDispatch();


    const { userName, price, token, products } = useSelector(reducers => reducers.userReducer);


    async function getCart() {
        try {
            const { data } = await axios.get("https://route-ecommerce.onrender.com/api/v1/cart", {
                headers: {
                    token: token,
                }
            })
            dispatcher(setProducts(data.data.products));
        } catch (error) {
            
        }
    }

    async function deleteProduct(idProduct) {
        try {
            const { data } = await axios.delete(`https://route-ecommerce.onrender.com/api/v1/cart/${idProduct}`, {
                headers: {
                    token: token,
                }
            });
            dispatcher(setProducts(data.data.products));
            dispatcher(setCountCart(data.numOfCartItems));
            dispatcher(setPrice(data.data.totalCartPrice));
            displayMessage("success", "Done");
        } catch (err) {
            displayMessage("error", "there is a problem");
        }
    }

    async function updateQuantity(idProduct, newCount) {

        try {
            const { data } = await axios.put(`https://route-ecommerce.onrender.com/api/v1/cart/${idProduct}`,
                {
                    count: newCount,
                },
                {
                    headers: {
                        token: token,
                    }
                });
            dispatcher(setCountCart(data.numOfCartItems));
            dispatcher(setPrice(data.data.totalCartPrice));
            dispatcher(setProducts(data.data.products));
            displayMessage("success", "Done");
        }
        catch (err) {
            displayMessage("warning", "maybe there is a problem");
        }

    }

    useEffect(() => {
        getCart();
    }, []);

    useLayoutEffect(() => {
        dispatcher(whatIsActive());
    }, []);

    return <>
        <Helmet>
            <title>Cart</title>
        </Helmet>
        <div className={styles.cart + " container-fluid"}>
            <div className="container">
                <span className={styles.name + " mb-3 d-block h3"}>Welcome , {userName} </span>
                <button className={styles.clearCart + " d-block h6"}
                    onClick={() => {
                        dispatcher(clearCart(token));
                    }}><i className="fa-solid fa-broom"></i> Clear your cart</button>
                <span className='d-block fw-bold'><i className="me-2 fa-solid fa-sack-dollar"></i> ${price} EGP</span>
            </div>
            <div className={styles.items + ' container py-5'}>

                {
                    (products && products.length != 0) ?
                        products.map((e, i) => {
                            return <div key={i} className={styles.item + " d-flex align-items-center py-1 mb-2"}>
                                <div className={styles.idItem + " text-center"}>
                                    {i + 1}
                                </div>
                                <div className='d-flex flex-grow-1'>
                                    {e.product.imageCover ? <img src={e.product.imageCover} className='w-100 me-4' alt="" /> : null}
                                    <div className='d-flex flex-column justify-content-center'>
                                        <span className='d-block h6'>{e.product.title ? e.product.title.split(" ").splice(0, 3).join(" ") : "test"}</span>
                                        <span className='d-block h6'> {e.price} $</span>
                                    </div>
                                </div>
                                <div className='ms-3 d-flex'>
                                    <button className='' onClick={() => {
                                        ;
                                        deleteProduct(e.product.id)
                                    }}><i className="fa-solid fa-trash"></i></button>
                                    <button className='mx-2' onClick={() => {
                                        updateQuantity(e.product.id, e.count + 1);
                                    }}><i className="fa-solid fa-caret-up"></i></button>
                                    {e.count > 1 ? <button onClick={() => { updateQuantity(e.product.id, e.count - 1); }}><i className="fa-solid fa-caret-down"></i></button> : null}
                                    <span className='mx-2'>{e.count}</span>
                                </div>
                            </div>
                        })
                        :
                        <div className='d-flex flex-column  align-items-center'>
                            <span className='d-block h1'> Your Cart Is Empty</span>
                            <img src={require("./../../assets/emptyCart.png")} alt="" />
                        </div>
                }

            </div>
        </div>
    </>
}
